import React, { useEffect, useCallback } from 'react';
import { Button } from 'react-bootstrap';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import { fetchFoodCategory, getFilteredFoodList } from '../services/FoodAPI';

export default function FilterButtons({ type }) {
  const dispatch = useDispatch();
  const recipes = useSelector((state) => state.recipes);
  const { categories } = recipes;

  useEffect(() => {
    dispatch(fetchFoodCategory(type));
  }, [dispatch, type]);

  const handleFilterButton = (selectedCategory) => {
    const cat = recipes.filteredCategory === selectedCategory ? 'All' : selectedCategory;
    dispatch(getFilteredFoodList(cat, type));
  };

  const replaced = useCallback(
    (category) => {
      const drinks = {
        'Milk / Float / Shake': 'Milk Shake',
        'Other/Unknown': 'Others',
        'Ordinary Drink': 'Ordinary',
        All: 'All',
        Cocktail: 'Cocktail',
        Cocoa: 'Cocoa',
      };
      return drinks[category];
    },
    [],
  );

  const variants = {
    1: 'outline-primary',
    2: 'outline-secondary',
    3: 'outline-success',
    4: 'outline-danger',
    5: 'outline-warning',
    6: 'outline-info',
    7: 'outline-light',
    8: 'outline-dark',
  };

  const filterButtons = () => categories[type].map((categoryName, i) => {
    const repl = type === 'drinks' ? replaced(categoryName) : categoryName;
    return (
      <Button
        variant={ variants[i] }
        key={ categoryName }
        onClick={ () => handleFilterButton(categoryName) }
        data-testid={ `${categoryName}-category-filter` }
        className="filter-button"
      >
        {repl}
      </Button>);
  });

  return (
    <div className="filter-buttons">
      {filterButtons()}
    </div>

  );
}

FilterButtons.propTypes = {
  type: PropTypes.string.isRequired,
};
